import { Routes } from '@angular/router';

import { SAlojamientoComponent } from './salojamiento/salojamiento.component';
import { SCoworkingComponent } from './scoworking/scoworking.component';
import { SHotelComponent } from './shotel/shotel.component';
import {GuardGuard} from '../guard.guard';

export const ServiciosRoutes: Routes = [
  {
    path: '',
    children: [
      {
        path: 'alojamiento',
        component: SAlojamientoComponent,
        data: {
          title: 'Alojamiento',
          urls: [
            { title: 'Alojamiento', url: '/servicios'},
            { title: 'Alojamiento' }
          ]
        }
        ,canActivate:[GuardGuard]
      },
      {
        path: 'coworking',
        component: SCoworkingComponent,
        data: {
          title: 'Coworking',
          urls: [
            { title: 'Coworking', url: '/servicios' },
            { title: 'Coworking' }
          ]
        }
        ,canActivate:[GuardGuard]
      },
      {
        path: 'hotel',
        component: SHotelComponent,
        data: {
          title: 'Hotel',
          urls: [
            { title: 'Hotel', url: '/servicios' },
            { title: 'Hotel' }
          ]
        }
        ,canActivate:[GuardGuard]
      }
    ]
  }
];


